'use client';

import { 
  Box, 
  Typography, 
  Paper, 
  Button, 
  Divider, 
  Chip, 
  Link as MuiLink,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Checkbox,
  useTheme
} from '@mui/material';
import { 
  Code, 
  Article, 
  YouTube, 
  CheckCircle,
  OpenInNew
} from '@mui/icons-material';
import Link from 'next/link';

export default function ChapterDetail({ chapter, progress, onToggleComplete }) {
  const theme = useTheme();
  const { problem } = chapter;
  const isCompleted = progress[problem?.id];

  if (!problem) return null;

  const difficultyColor = {
    easy: 'success',
    medium: 'warning',
    hard: 'error'
  }[problem.level];

  return (
    <Paper 
      elevation={0} 
      sx={{ 
        p: 3, 
        backgroundColor: 'background.paper',
        borderRadius: 2,
        height: '100%'
      }}
    >
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 3 }}>
        <Box>
          <Typography variant="caption" color="text.secondary" sx={{ mb: 1, display: 'block' }}>
            {chapter.title}
          </Typography>
          <Typography variant="h5" component="h2" sx={{ mb: 1, fontWeight: 600 }}>
            {problem.title}
          </Typography>
          <Chip 
            label={problem.level} 
            color={difficultyColor} 
            size="small" 
            variant="outlined"
            sx={{ 
              textTransform: 'capitalize',
              fontWeight: 500
            }}
          />
        </Box>
        
        <Box>
          <Checkbox
            checked={isCompleted}
            onChange={() => onToggleComplete(chapter.id, problem.id)}
            icon={<CheckCircle />}
            checkedIcon={<CheckCircle color="success" />}
            sx={{ p: 0 }}
          />
        </Box>
      </Box>

      <Divider sx={{ my: 2 }} />

      <Box sx={{ mb: 4 }}>
        <Typography variant="subtitle1" sx={{ mb: 2, fontWeight: 600 }}>
          Problem Description
        </Typography>
        <Typography variant="body1" paragraph>
          {getProblemDescription(problem.id)}
        </Typography>
      </Box>

      <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 2, mb: 4 }}>
        <Button
          variant="outlined"
          color="primary"
          startIcon={<Code />}
          endIcon={<OpenInNew fontSize="small" />}
          component={MuiLink}
          href={`https://leetcode.com/problems/${problem.id.replace(/-/g, '-')}/`}
          target="_blank"
          rel="noopener noreferrer"
        >
          Solve on LeetCode
        </Button>
        
        <Button
          variant="outlined"
          color="secondary"
          startIcon={<YouTube />}
          endIcon={<OpenInNew fontSize="small" />}
          component={MuiLink}
          href={`https://www.youtube.com/results?search_query=${encodeURIComponent(problem.title + ' solution')}`}
          target="_blank"
          rel="noopener noreferrer"
        >
          Watch Solution
        </Button>
        
        <Button
          variant="outlined"
          startIcon={<Article />}
          endIcon={<OpenInNew fontSize="small" />}
          component={MuiLink}
          href={`https://www.geeksforgeeks.org/${problem.id.replace(/-/g, '-')}/`}
          target="_blank"
          rel="noopener noreferrer"
        >
          Read Article
        </Button>
      </Box>

      <Box>
        <Typography variant="subtitle1" sx={{ mb: 2, fontWeight: 600 }}>
          Similar Problems
        </Typography>
        <List dense>
          {getSimilarProblems(chapter, problem.id).map((similar) => (
            <ListItem 
              key={similar.id} 
              disablePadding 
              secondaryAction={
                <Chip 
                  label={similar.level} 
                  size="small" 
                  color={difficultyColor}
                  variant="outlined"
                  sx={{ 
                    textTransform: 'capitalize',
                    fontSize: '0.65rem',
                    height: 20
                  }}
                />
              }
            >
              <MuiLink 
                component={Link} 
                href={`/dashboard?problem=${similar.id}`} 
                color="inherit" 
                underline="hover"
                sx={{ 
                  display: 'flex', 
                  alignItems: 'center', 
                  width: '100%',
                  py: 1,
                  px: 2,
                  borderRadius: 1,
                  '&:hover': {
                    backgroundColor: 'action.hover',
                  }
                }}
              >
                <ListItemText 
                  primary={similar.title} 
                  primaryTypographyProps={{ 
                    variant: 'body2',
                    color: 'text.primary'
                  }} 
                />
              </MuiLink>
            </ListItem>
          ))}
        </List>
      </Box>
    </Paper>
  );
}

// Helper functions (you can replace these with actual data)
function getProblemDescription(problemId) {
  const descriptions = {
    'two-sum': 'Given an array of integers nums and an integer target, return indices of the two numbers such that they add up to target.',
    'reverse-list': 'Reverse a singly linked list and return the head of the reversed list.',
    // Add more problem descriptions
  };
  return descriptions[problemId] || 'Problem description not available.';
}

function getSimilarProblems(chapter, currentProblemId) {
  return chapter.problems
    .filter(p => p.id !== currentProblemId)
    .slice(0, 3); // Return max 3 similar problems
}