'use client';

import { useState, useEffect } from 'react';
import { Box, Container, Typography, Paper, useTheme, useMediaQuery } from '@mui/material';
import dynamic from 'next/dynamic';
import ChapterDetail from '@/components/DSA/ChapterDetail';

// Dynamically import components for code splitting
const DSAChapterList = dynamic(() => import('@/components/DSA/ChapterList'), { ssr: false });
const DSAProgressTracker = dynamic(() => import('@/components/DSA/ProgressTracker'), { ssr: false });

export default function Dashboard() {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const [selectedChapter, setSelectedChapter] = useState(null);
  const [progress, setProgress] = useState({});

  // Load progress from localStorage on component mount
  useEffect(() => {
    const savedProgress = localStorage.getItem('dsaProgress');
    if (savedProgress) {
      setProgress(JSON.parse(savedProgress));
    }
  }, []);

  // Save progress to localStorage whenever it changes
  useEffect(() => {
    localStorage.setItem('dsaProgress', JSON.stringify(progress));
  }, [progress]);

  const handleToggleComplete = (chapterId, problemId) => {
    setProgress(prev => ({
      ...prev,
      [chapterId]: {
        ...prev[chapterId],
        [problemId]: !prev[chapterId]?.[problemId]
      }
    }));
  };

  return (
    <Container maxWidth="xl" sx={{ py: 4 }}>
      <Box sx={{ mb: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          DSA Learning Path
        </Typography>
        <Typography variant="subtitle1" color="text.secondary">
          Track your progress and master Data Structures and Algorithms
        </Typography>
      </Box>

      <Box sx={{ display: 'flex', flexDirection: isMobile ? 'column' : 'row', gap: 3 }}>
        {/* Left Sidebar - Chapter List */}
        <Box sx={{ 
          width: isMobile ? '100%' : '30%',
          minWidth: isMobile ? '100%' : 300
        }}>
          <DSAChapterList 
            onSelectChapter={setSelectedChapter} 
            selectedChapter={selectedChapter}
            progress={progress}
          />
        </Box>

        {/* Main Content - Problem Details */}
        <Box sx={{ 
          flex: 1,
          minWidth: 0 // Prevents flex item from overflowing
        }}>
          {selectedChapter ? (
            <ChapterDetail 
              chapter={selectedChapter} 
              progress={progress[selectedChapter.id] || {}}
              onToggleComplete={handleToggleComplete}
            />
          ) : (
            <Paper 
              elevation={0} 
              sx={{ 
                p: 3, 
                height: '100%', 
                display: 'flex', 
                alignItems: 'center', 
                justifyContent: 'center',
                backgroundColor: 'background.paper',
                borderRadius: 2
              }}
            >
              <Typography variant="h6" color="text.secondary">
                Select a chapter to view problems
              </Typography>
            </Paper>
          )}
        </Box>

        {/* Right Sidebar - Progress Tracker */}
        {!isMobile && (
          <Box sx={{ width: 300 }}>
            <DSAProgressTracker progress={progress} />
          </Box>
        )}
      </Box>
    </Container>
  );
}